import uniBadge from '@/components/uni-badge/uni-badge.vue';
export default {
	components: { uniBadge },
	data() {
		return {
			user: {},
			token: '',
			oneData: {
				//1级分类的数据
				index: 0, //1级分类的选中项
				data: [], //1级分类的数据
				nameArr: ['请选择'], //1级分类的名称
				newCount: 0 //1级分类的数量
			},
			twoData: {
				//1级分类的数据
				index: 0, //1级分类的选中项
				data: [], //1级分类的数据
				nameArr: ['请选择'], //1级分类的名称
				newCount: 0 //1级分类的数量
			},
			threeData: {
				//1级分类的数据
				index: 0, //1级分类的选中项
				data: [], //1级分类的数据
				nameArr: ['请选择'], //1级分类的名称
				newCount: 0 //1级分类的数量
			}
		};
	},
	onLoad(e) {
		this.user = JSON.parse(e.list);
		this.token = e.token;
	},
	onShow() {
		this.tabList('one');
	},
	methods: {
		tabList(type) {
			let url = '';
			const data = {
				teacherId: this.user.id,
				token: this.token
			};
			let typeN = type + 'Data';
			if (type == 'one') {
				url = 'manageUserUploads/selectHabit.json';
			} else if (type == 'two') {
				url = 'manageUserUploads/selectHabitClock.json';
				let index = this.oneData.index;
				console.log(index);
				console.log(this.oneData.data[index]);
				data.typeId = this.oneData.data[index - 1].id;
			} else if (type == 'three') {
				url = 'manageUserUploads/selectHabitClockInfo.json';
				let index = this.twoData.index;
				data.clockId = this.twoData.data[index - 1].id;
			}
			this.uniHttp.getJSON(url, data, res => {
				console.log(res);
				if (res.data.success) {
					let myData = res.data.data;
					this[typeN].data = myData;
					this[typeN].nameArr = ['请选择'];
					for (let i = 0; i < myData.length; i++) {
						this[typeN].nameArr.push(myData[i].name);
					}
					this[typeN].newCount = myData.newCount;
					this[typeN].index = 0;
				}
			});
		},
		bindPickerOneChange(e) {
			if (e.target.value != this.oneData.oneindex) {
				this.oneData.index = parseInt(e.target.value);
				console.log(this.oneData.index);
				if (this.oneData.index > 0) {
					this.tabList('two');
				} else {
					this.twoData = {
						//1级分类的数据
						index: 0, //1级分类的选中项
						data: [], //1级分类的数据
						nameArr: ['请选择'], //1级分类的名称
						newCount: 0 //1级分类的数量
					};
					this.threeData = {
						//1级分类的数据
						index: 0, //1级分类的选中项
						data: [], //1级分类的数据
						nameArr: ['请选择'], //1级分类的名称
						newCount: 0 //1级分类的数量
					};
				}
			}
		},
		bindPickerTwoChange(e) {
			if (e.target.value != this.oneData.oneindex) {
				this.twoData.index = parseInt(e.target.value);
				if (this.oneData.index > 0) {
					this.tabList('three');
				} else {
					this.threeData = {
						//1级分类的数据
						index: 0, //1级分类的选中项
						data: [], //1级分类的数据
						nameArr: ['请选择'], //1级分类的名称
						newCount: 0 //1级分类的数量
					};
				}
			}
		},
		bindPickerThreeChange(e) {
			if (e.target.value != this.oneData.oneindex) {
				this.threeData.index = parseInt(e.target.value);
			}
		}
	}
};
